/**
* 案件->添加tree
* @module 案件
* @desc 角色人员组件
* @author 乔亚军
* @date 2019-6-30
**/
<template>
    <div>
        <el-dialog :close-on-click-modal='false' :title="title" :visible="show" @close="closeDialog" center append-to-body
                   width="650px"  top="30px">
            <div class="wrop" v-loading='loading'>
                <el-form :model="form" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                    <el-form-item label="角色名称：" prop="roleName" >
                        <el-input  v-model="form.roleName" v-if="isDisable" ></el-input>
                        <span v-if="see">{{form.roleName}}</span>
                        <span v-if="isEdit">{{form.roleName}}</span>
                    </el-form-item>
                </el-form>

                <el-tree
                    v-if="show"
                    :data="treeList"
                    show-checkbox
                    node-key="orgId"
                    :props="{label:'orgName'}"
                    accordion
                    :default-checked-keys="defaultArr"
                    :default-expanded-keys="expandedArr"
                    ref="tree"
                    >
                </el-tree>
            </div>
            <span class="dialog-footer" slot="footer">
                
                <el-button @click="AddEdit('ruleForm')" size="small" type="primary" v-if="!see"><i
                        class="iconfont icon-save"></i>确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
    import {apiOrgSearch,} from "../../../../api/system";
    import {apiCommonGetOrgUser,apiCaseRoleProcessAddRoleUser,apiCaseRoleProcessGet,apiCaseRoleProcessUpdate} from "../../../../api/case";
    import {getTree} from "../../../../until/menuList";
    import md5 from 'js-md5'
import { debug } from 'util';

    let that;
    export default {
        name: 'see',
        props: {
            show: {
                type: Boolean,
                default: false
            },
            title: {
                type: String,
                default: ''
            },
            see:{
               type: Boolean,
               default: false
            },
            editId:{
               default: null
            },
            dictName: {
                type: String,
                default: ''
            },
            isDisable:{
                type:Boolean,
                default: false

            },
            isEdit:{
                type:Boolean,
                default:false
            }
            

        },
        data() {
            return {
                rules: {
                    roleName: [
                        { required: true, message: '请输入角色名称', trigger: 'blur' },
                    ],
                },
                loading:true,
                treeList:[
                     {
                        orgId: "1",
                        orgName: "国家电网",
                        status: null,
                        isCheck: null,
                        children: []
                    }
                ],
                defaultArr:[],
                expandedArr:[],
                selectData:null,
                edit:false,
                form:{
                    roleName:''
                }
            }
        },
        methods: {
            //关闭弹窗
            closeDialog() {
                this.$emit('update:show', false)
            },

            // 添加
            AddEdit(formName){
                this.$refs[formName].validate((valid) => {
                        if (valid) {
                            let selectData = this.$refs.tree.getCheckedNodes(true);
                            let obj = {};
                            obj.roleName = this.form.roleName;
                            obj.userList = selectData;

                            if(this.dictName){
                                //修改
                            apiCaseRoleProcessUpdate(obj).then(res => {
                                    this.$message({
                                        type: 'success',
                                        message: '修改成功 !'
                                    });
                                    this.$emit('getAccountList');
                            });
                            }else{
                                //添加

                                apiCaseRoleProcessAddRoleUser(obj).then(res => {
                                    this.$message({
                                        type: 'success',
                                        message: '添加角色成功 !'
                                    });
                                    this.$emit('getAccountList');

                                });
                            }
                
                            this.closeDialog();
                    } else {
                        this.$message({
                            type: 'warning',
                            message: '请填写角色名称 !'
                        });
                        return false;
                    }
                });
                

            },
           initPage(){
               
            //组织部门
            apiOrgSearch().then(res => {
                res.data.forEach(i => {
                    i.name = i.orgName
                });
               this.treeList = getTree(res.data, 'orgId', 'parentCode', 'isParent');
               this.treeList.forEach(item=>{
                    item.children.forEach(item2=>{
                        item2.children.forEach(item3=>{
                            apiCommonGetOrgUser(item3.orgId).then(res => {
                                res.userList.forEach(i=>{
                                    i.a=i.orgName;
                                    i.b =i.orgId;
                                    i.orgName=i.fullName;
                                    i.orgId =i.loginId;
                                });
                                item3.children= res.userList;
                                this.loading=false
                                
                            });
                        });
                    });
                });
            });
            
           },
           getSetRoles(){
                this.form.roleName=this.dictName;                
                apiCaseRoleProcessGet({roleName:this.dictName}).then(res => {

                    let idArr = [];
                    if(res.data){

                        res.data.forEach(item=>{
                            idArr.push(item.orgId)
                        });
                        this.defaultArr = idArr;
                    }else{

                    }
                });
           }

        },
       
        created(){
            this.initPage();
        },
      watch:{
        show(val){
            if(val){
                this.defaultArr=[];
                 if(this.dictName){
                      this.getSetRoles();
                }else{
                    this.form.roleName='';
                };
                 this.$nextTick(() => {
                    this.$refs.ruleForm.clearValidate();
                });
               
                this.expandedArr=["06414fb27d1e400995f85c14fa15b091"]
            }
        },
       
    }
    }
</script>
<style lang='less' scoped>
    .wrop {
        // min-height: 500px;
       
    }
</style>


