<!--/**
* 配置->配置部门人员
* @module 配置中心
* @desc 角色人员列表组件
* @author 乔亚军
* @date 2019-6-29
**/-->
<template>
    <div>
        <div class="wrop-search">
            <lj-search-from :searchObj="searchObj" @searchList="searchList" :onAdd="onAdd"/>
        </div>
        <el-table :data="list" :header-cell-style="{background:'#e9eff8'}" :row-style="{cursor:'pointer'}"
                   border size="mini" v-loading='loading'>
           
            <el-table-column
                prop="roleName"
                label="角色名称"
                align="center"
               >
            </el-table-column>
            
           <el-table-column align="center" label="操作" width="260">
                <template slot-scope="scope">
                    <el-tooltip class="item" content="查看" effect="dark" placement="top-end">
                        <el-link @click.stop="seeRoleFun(scope.row)" class="el-icon-search btn-link" type="success"><i></i></el-link>
                    </el-tooltip>
                    <el-tooltip class="item" content="修改配置人员" effect="dark" placement="top-end">
                        <el-link @click.stop="editRoleFun(scope.row)" class="el-icon-edit btn-link" type="primary"><i></i></el-link>
                    </el-tooltip>
                    <el-tooltip class="item" content="删除" effect="dark" placement="top-end">
                        <el-link @click.stop="delRoleFun(scope.row)" class="el-icon-delete btn-link" type="warning"><i></i></el-link>
                    </el-tooltip>
                </template>
            </el-table-column>
        </el-table>
        <set-role :see='editObj.see' :isDisable='editObj.isDisable' :isEdit='editObj.isEdit' :dictName="editObj.dictName" @getAccountList='getAccountList' :show.sync='editObj.show' :title='editObj.title' :editId='editObj.editId'></set-role>
    </div>
</template>
<script>
import SetRole from './components/setrole';
import {apiCaseRoleProcessSarch,apiCaseRoleProcessDelete} from "../../../api/case";
import LjSearchFrom from '../../../components/LjSearchForm';
import LjPagination from '../../../components/LjPagination';
export default {
    data(){
        return{
            loading:true,
            list: [],
            editObj:{
               show:false,
               dictName:null,
               title:'',
               see:false,
               editId:false,
               isEdit:false
            },
            addObj:{
               show:false,
               title:'',
               addId:null
            },
            form: {
                pageSize: 20,
                pageNum: 1,
            },
            searchObj:null,
        }
    },
    components:{SetRole,LjSearchFrom,LjPagination},
    methods:{
        //修改
       editRoleFun(row){
           this.editObj.dictName = row.roleName;
           this.editObj.show=true;
           this.editObj.title = '配置角色';
           this.editObj.see = false;
           this.editObj.isEdit = true;
           this.editObj.isDisable=false;
       },
        // 添加
        onAdd(){
           this.editObj.show=true;
           this.editObj.title = '添加配置角色';
           this.editObj.dictName= null;
           this.editObj.see = false;
           this.editObj.isDisable=true;
           this.editObj.isEdit = false;

        },
       //查看
       seeRoleFun(row){
           this.editObj.dictName = row.roleName;
           this.editObj.show=true;
           this.editObj.title = '查看配置角色'
           this.editObj.see = true;
           this.editObj.isDisable=false;
           this.editObj.isEdit = false;
       },
       //查询+搜索
        getAccountList() {
            apiCaseRoleProcessSarch(this.form).then(res => {
                this.list = res.data;
                this.loading=false;
            })
        },
        //删除
        delRoleFun(row){
           this.loading = true;
           this.$confirm("此操作将永久删除该数据, 是否继续?", "提示", {
                    confirmButtonText: "确定",
                    cancelButtonText: "取消",
                    type: "warning"
                }).then(() => {
                    apiCaseRoleProcessDelete({roleName:row.roleName}).then(() => {
                        this.getAccountList();
                        this.$message({
                            type: "success",
                            message: "删除成功!"
                        });
                       this.loading = false;

                    });
                });
        },
        //搜索
        searchList(data) {  //data就是接收的this.form数据
             this.loading = true;
             this.form = data;
             this.form.pageNum = 1;
             console.log(data)
            apiCaseRoleProcessSarch(data).then(res => {
                console.log(res)
                this.list = res.data;
                this.loading=false
            })

        },
    },
     
    created(){
        this.getAccountList()
    }
}
</script>
<style lang="less" scoped>
    .btn-link {
        margin: 0 10px
    }
</style>


